.proposal {
  text-align: left;

  h1,
  h3 {
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 10px;
    line-height: 1.2;
  }
  hr {
    border: none;
  }
  p {
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 1.5;
  }
  .refWrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    h3 {
      font-size: 20px;
    }
    a {
      color: #1155cc;
    }
  }
  img {
    width: 90%;
    margin: 0 auto 15px auto;
  }
}
