.ProposalList {
  position: relative;
  width: 100%;
  margin-top: 40px;
}

.proposalItem {
  position: relative;
  width: 100%;
  display: flex;
  align-items: flex-start;
  column-gap: 40px;
  background: #f4f4f4;
  border-radius: 12px;
  padding: 10px;
  .status {
    position: absolute;
    right: 0;
    top: 0;
    font-size: 12px;
    font-weight: 700;
    border-radius: 15px;
    background-color: #00c466;
    padding: 4px 15px 1px;
    color: white;
    text-transform: capitalize;
    &.ended {
      background-color: #afafaf;
    }
  }

  .proposalContent {
    position: relative;
    flex-grow: 1;
    text-align: left;
    padding: 10px 20px;
    h2 {
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
      text-transform: capitalize;

      color: #01a7a8;
    }
    h1 {
      font-weight: 800;
      font-size: 18px;
      line-height: 28px;
      text-transform: uppercase;

      color: #000000;
      margin-bottom: 10px;
    }

    .infoItem {
      display: flex;
      align-items: center;
      line-height: 14px;
      padding: 10px 0;
      span {
        font-weight: 500;
        font-size: 14px;
        text-transform: capitalize;
        color: #000000;
        opacity: 0.6;
        margin-right: 5px;
        display: flex;
        align-items: center;
        gap: 5px;
      }
      font-weight: 600;
      font-size: 14px;
      text-transform: uppercase;
      color: #59d89d;
      img {
        width: 14px;
        margin-top: -3px;
      }
    }
    .timeWrap {
      display: flex;
      column-gap: 20px;
    }
    .percentBar {
      width: 100%;
    }
  }
}

@media (max-width: 765px) {
  .ProposalList {
    margin-top: 20px;
  }

  .proposalItem {
    column-gap: 0;
    row-gap: 10px;
    .status {
      font-size: 10px;
      border-radius: 15px;
      padding: 4px 10px 1px;
    }

    .proposalContent {
      position: relative;
      flex-grow: 1;
      text-align: left;
      padding: 10px;

      h1 {
        font-size: 14px;
        line-height: 20px;
      }

      .infoItem {
        display: flex;
        justify-content: space-between;
      }
      .timeWrap {
        display: flex;
        column-gap: 0;
        row-gap: 2px;
        flex-direction: column;
      }
    }
  }
}
