@use '../../../variables' as var;
.voteResult {
  position: absolute;
  left: 0;
  top: 0;
  transform: translate(-25%, -25%);
  z-index: 1;
}
.OptionItem {
  position: relative;
  width: 100%;
  height: 80px;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(18, 16, 27, 0.1);
  box-sizing: border-box;
  border-radius: var.$border-radius;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  column-gap: 10px;
  cursor: pointer;
  overflow: hidden;
  &:hover,
  &.selected {
    border: 2px solid var.$color-primary;
    .optionState {
      border: 4px solid var.$color-primary;
    }
  }
  &.disabled {
    background-color: #f4f4f4;
    pointer-events: none;
    opacity: 0.8;
  }

  .optionState {
    position: relative;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 4px solid #dedede;
    display: flex;
    align-items: center;
    justify-content: center;
    &.voted {
      background-color: var.$color-primary;
    }
    flex-shrink: 0;
  }
  .textWrap {
    // width: 100%;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 3px;
    .optionName {
      font-weight: 700;
      font-size: 14px;
      line-height: 14px;
      /* identical to box height, or 100% */
      color: #000000;
      margin: 0;
      text-transform: none;
    }
  }
  .valueWrap {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      color: rgba(18, 16, 27, 0.5);
    }
    .percent {
      font-weight: 500;
      font-size: 14px;
      line-height: 16px;
      margin: 0;
    }
  }
  .percentBarOutter {
    width: 100%;
    border-radius: 10px;
    height: 8px;
    background-color: #e6e6e6;
  }
  .percentBar {
    border-radius: 10px;
    background-color: var.$color-primary;
    height: 100%;
  }
}
