@use '../../variables' as var;

.VoteDetail {
  position: relative;
  width: 100%;
  padding-top: 75px;
  background-color: #ededed;
  min-height: 100vh;
}
.loadingPlaceholder {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #ededed;
}
.banner {
  background: var.$color-header-background;
}
.bannerContainer {
  padding: 30px 0;
  display: flex;
  justify-content: flex-start;
  max-width: 1100px;
}
.backButton {
  display: flex;
  align-items: center;
  color: white;
  font-size: 20px;
  gap: 5px;
  span {
    line-height: 20px;
  }
}
.mainContent {
  display: flex;
  align-items: flex-start;
  gap: 15px;
}
.descWrap {
  width: 100%;
  flex: 1;
}
.actionWrap {
  width: 30%;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}
.actionCard {
  width: 100%;
  background-color: white;
  border-radius: var.$border-radius;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.titleWrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  h2 {
    font-size: 20px;
    font-weight: 700;
  }
}
.optionWrap {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.statsItem {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0;
  h2 {
    font-size: 14px;
    color: rgba(18, 16, 27, 0.5);
    line-height: 14px;
    display: flex;
    align-items: center;
    gap: 5px;
  }
  p {
    font-size: 14px;
    display: flex;
    align-items: center;
    line-height: 14px;
  }
  img {
    margin-top: -3px;
    width: 14px;
    margin-left: 5px;
  }
}
.voteButton {
  width: 100%;
}
.stakeModal {
  width: 100%;
  padding: 10px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
.balance {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0 5px;
  font-size: 14px;
  margin-bottom: 5px;
  color: #12101b;
  opacity: 0.5;
  :first-child {
    font-weight: 700;
  }
}
.status {
  font-size: 12px;
  font-weight: 700;
  border-radius: 15px;
  background-color: #00c466;
  padding: 4px 15px 1px;
  color: white;
  text-transform: capitalize;
  &.ended {
    background-color: #afafaf;
  }
}

.forumButton {
  display: block;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: rgba(211, 211, 211, 0.39);
  margin-top: 10px;
  cursor: pointer;
  font-weight: 500;
  user-select: none;
  border: 1px solid rgba(211, 211, 211, 0.39);
  &:hover {
    border-color: grey;
  }
  a {
  }
  .forumLinkIcon {
    width: 15px;
    margin-left: 5px;
  }
}

@media (max-width: var.$mobile-break-point) {
  .VoteDetail {
    padding-top: 60px;
  }
  .mainContent {
    flex-direction: column;
  }
  .actionWrap {
    width: 100%;
  }
}
