.Vote {
  position: relative;
  padding-top: 75px;
  padding-bottom: 50px;
  background-color: white;
  min-height: 100vh;
}

.bannerWrap{
  background: #212222;

}
.mainContent {
  width: 800px;
  max-width: calc(100% - 30px);
  margin: 0 auto;
}

.banner{
  width: 1200px;
  max-width: calc(100% - 30px);
  margin: 0 auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 25px;
  color: white;
}

.voteImg{
  width: auto;
  height: 250px;
}

.voteImgMobile{
  display: none;
}

.titleWrap{
  text-align: left;
  width: 70%;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  h1{
    font-style: normal;
    font-weight: 600;
    font-size: 48px;
    color: white;
  }
  h2{
    color: white;
    font-size: 14px;
  }
}
.title {
  font-style: normal;
  font-weight: 900;
  font-size: 36px;
  line-height: 40px;
  text-transform: capitalize;
  margin-bottom: 20px;
}
.navLinkWrap {
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0 0 0 auto;
  .navLink {
    margin-right: 10px;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    h2 {
      font-weight: 600;
      font-size: 16px;
      margin: 0;
      color: white;
      border-radius: 10px;
      padding: 5px 15px 4px;
    }
    &.navLinkActive,
    &:hover {
      h2{
        background: #59D89D;
      }
    }
  }
}
@media (max-width: 765px) {
  .Vote {
    padding-top: 60px;
  }

  .banner{
    flex-direction: column;
    gap: 15px;
  }

  .voteImg{
    display: none;
  }
  .voteImgMobile{
    width: auto;
    height: 50px;
    display: flex;
  }
  .titleInnerWrap{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .titleWrap{
    width: 100%;
    display: flex;
    align-items: center;
    h1{
      font-style: normal;
      font-weight: 600;
      font-size: 25px;
      color: white;
    }
    h2{
      color: white;
      font-size: 14px;
      line-height: 18px;
      padding: 0 15px;
      text-align: justify;
    }
  }
  .title {
    margin-bottom: 0;
    margin-left: 20px;
  }
  .navLinkWrap {
    margin: 0 auto 20px auto;
    .navLink {
      margin: 0 5px;
      h2 {
        font-size: 12px;
        border-radius: 7px;
        padding: 4px 15px 2px;
      }
    }
  }
}
